@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
@import url(https://cdn.jsdelivr.net/npm/bulma-carousel@4.0.3/dist/css/bulma-carousel.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aliceblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hero {
    height: 500px;
    background-image: url("https://www.educaciontrespuntocero.com/wp-content/uploads/2020/02/manualidades-medioambiente.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

@media (max-width: 600px) {
    .hero {
        
        background-image: url("https://static.vecteezy.com/system/resources/previews/002/667/607/non_2x/world-shopping-bag-and-bottle-environment-ecology-cartoon-design-vector.jpg");
     
        background-position:center;
        background-size: cover;
        
    
    }

    .title24{
        color: #111827 !important;
    }
}

.is-transparent {
    background-color: rgba(42, 42, 42, 0.644) !important;
}

.bgg {
    background-color: #71cc49 !important;
}

.is-active {
    border-radius: 5px 5px 5px 5px !important;
}

.navbar-item:hover {
    border-radius: 5px 5px 5px 5px !important;
}

.title24{
    color: #111827 !important;
}

